body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.-b-body{
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.-b-sidenav-menu{
  background-color: #333;
  height: 960px;
  color: white;
}
.-b-side{
  width: 15%;
  /* background-color: #2C497F; */
  background-color: #05204A; /* oxford blue */
  height: 100%;
  position: fixed;
  left: 0;
}
.-b-main{
  width: 85%;
  height: 1500px;
  margin-left: 15%;
  padding-top: 50px;
}
.-b-main-head{
    font-family: cursive;
    font-weight: bold;
    margin-top: 60px;
    color: white;
} 
/*  */
.-b-accrodion{
    margin-top: 40px;
}
.-b-nav-panel{
    border:none;
}
.-b-nav-panel-heading{
    background-color: #05204A !important;
    color: white !important;
}
.-b-nav-panel-title > a:hover, a:focus{
    text-decoration: none !important;
}
.-b-nav-panel-title {
    font-size: 22px !important;
}
.-b-nav-panel-body{
    background-color: #05204A;
    border: none !important;
    padding: 0px !important;
}
.-b-sidenav-ul{
    -webkit-padding-start: 38px !important;
            padding-inline-start: 38px !important;
}

.-b-sidenav-ul > li {
    list-style-type: none;
}
.-b-sidenav-ul > li > a {
    text-decoration: none;
    cursor: pointer;
    font-size: 18px;
    color: white;
}
/* Top nav */
.-b-top-nav{
    background-color: #05204A;
    border-color: #05204A;
}
.-b-top-nav-ul > li > a{
    color: white !important;
    background-color: #05204A !important;
}
.-b-login-left_img{
    height: 450px;
    background: url(/static/media/evergreen.5be530a0.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  .-b-input{
    margin-bottom: 22px;
    height: 50px;
    border: none;
    border-bottom: 2px solid #ccc;
    border-radius: 0px;
    box-shadow: none;
  }
  .-b-input:focus{
      border-color: #9e9e9e;
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(204, 204, 204, 0.6784313725490196);
  }
  .ip-icon{
      border: none;
      background-color: #fff;
      border-bottom: 2px solid #ccc;
  }
  .-b-input-group{
      margin-bottom: 26px;
  }
  .-b-breadCrumb{
      background-color: #fff;
      font-size: 20px;
      font-weight: bold;
      padding-top: 56px;
  }
  .-b-btn{
    color: #337ab7;
    border-radius: 25px;
    background-color: #fff;
    font-weight: bold;
    font-size: 18px;
    padding: 8px 40px;
    border: 2px solid #337ab7;
  }
  .-b-login-row{
      margin-top: 200px;
  }
  .-b-signup-link{
      margin-top: 36px;
  }
